.background-container {
  position: relative;
  min-height: 100vh;
  width: 100%;
  /* More pronounced radial gradient with greater color contrast */
  background: radial-gradient(
    circle at center,
    #3a3a60 0%,
    /* Brighter purple-blue center */ #25253e 30%,
    /* Mid-tone */ #141425 60%,
    /* Darker blue */ #0a0a12 100% /* Almost black edges */
  );
  background-attachment: fixed;
  overflow: hidden;
  z-index: 0;
}

.p5Canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  pointer-events: none; /* Ensure canvas doesn't capture clicks */
}

/* Ensure the canvas container is positioned properly */
.react-p5 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
}
